import React, { useState, Fragment, useEffect } from "react";
import { Link, withRouter, Redirect } from 'react-router-dom';
import { connect } from "react-redux";
import { createProfile, getCurrentProfile } from '../../../actions/profile'

const LawyerCreateProfile = ({ 
    createProfile,
     history,
     getCurrentProfile,
     profile: { profile, loading },
     }) => {
    const [formData, setFormData] = useState({
      lawyerKnow:"",
      experience:"",
      location: "",
      bio: "",
      skills: "",
      workReady:"",
      reactTime:""
    });

    const lawyersKnow = {
        diniAvoda:" ",
        diniNezikin:" ",
        diniMispaha:" ",
        diniTabora:" ",
        diniNadlan:" ",
        diniHozaLpoal:" "
    }

   
    const {
        lawyerKnow,
        experience,
        location,
        bio,
        skills,
        workReady,
        reactTime,
    } = formData;
    
      const onChange = e => setFormData({...formData, [e.target.name]: e.target.value});
    
      const onSubmit = e => {
        e.preventDefault()
        createProfile(formData, history)
      };
    
      useEffect(() => {
        getCurrentProfile();
      }, [getCurrentProfile]);
    
  
    return loading && profile === null ? (
        <Redirect to='/lawyerdashboard' />
      ) : (
        <div className="">
            <div className="text-center">
          <h1 className="large text-primary">  </h1>
          <p className="lead">
                   <i className="fas fa-user" /> 
          
          </p>
            </div>
            <div className="row justify-content-center mb-3">
          <form className="form text-center" onSubmit={e => onSubmit(e)}>
            <div className="form-group">
              <select name="lawyerKnow" value={lawyerKnow} onChange={e => onChange(e)}>
                <option value="0">*    </option>
                <option value={lawyersKnow.diniAvoda} > </option>
                <option value={lawyersKnow.diniNezikin}> </option>
                <option value={lawyersKnow.diniMispaha}> </option>
                <option value={lawyersKnow.diniTabora}> </option>
                <option value={lawyersKnow.diniNadlan}> </option>
                <option value={lawyersKnow.diniHozaLpoal}> </option>
              </select>
              <small className="form-text">

              </small>
            </div>
            <div className="form-group">
              <input type="text" placeholder=" " name="experience" value={experience} onChange={e => onChange(e)} />
              <small className="form-text">
                 
              </small>
            </div>
            <div className="form-group">
              <input type="text" placeholder=" " name="location" value={location} onChange={e => onChange(e)}/>
              <small className="form-text">
                 
              </small>
            </div>
            <div className="form-group">
              <input type="text" placeholder="* " name="skills" value={skills} onChange={e => onChange(e)}/>
              <small className="form-text">
                    (, , )
              </small>
            </div>
            
            <div className="form-group">
              <textarea placeholder="  " name="bio" value={bio} onChange={e => onChange(e)}/>
              <small className="form-text">    </small>
            </div>
            <div className="form-group">
              <input type="text" placeholder="   " name="workReady" value={workReady} onChange={e => onChange(e)}/>
              <small className="form-text">
                        
              </small>
            </div>
            <div className="form-group">
              <input type="text" placeholder="   " name="reactTime" value={reactTime} onChange={e => onChange(e)}/>
              <small className="form-text">
                   , 
              </small>
            </div>
            <input type="submit" className="btn btn-primary my-1" value="" />
            <Link className="btn btn-light my-1" to="/lawyerdashboard">
              
            </Link>
          </form>
          </div>
        </div>
      );
}


const mapStateToProps = state => ({
  profile: state.profile
});

export default connect(mapStateToProps, { createProfile, getCurrentProfile })(withRouter(LawyerCreateProfile));
